.Snapshot {
    font-size: .14rem;
    line-height: .14rem;
    font-family: Source Han Sans CN;
    font-weight: 400;
    color: #040E34;
    padding: .26rem;
    display: flex;
    flex-direction: column;

    .top {
        display: flex;
        justify-content: space-between;

        .top-text {
            margin-top: .18rem;
            color: #9696A3;
        }

        .top-btn {
            width: .88rem;
            height: .4rem;
            background: #5C5EE4;
            opacity: 1;
            border-radius: 4px;
            color: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
        }
    }

    .snapshot-table {
        margin-top: .12rem;
        border: 1px solid #D9DBE1;

        .more-operation {
            color: #5C5EE4;
            cursor: pointer;
            padding: .1rem;
            font-size: .18rem;
        }

        .host {
            display: flex;
            flex-direction: column;

            // .host-name {}

            .host-ip {
                color: #9696A3;
            }
        }

        .area {
            img {
                width: 0.26rem;

            }

            .country {
                margin-left: .14rem;
            }

            .city {
                margin-left: .05rem;
            }
        }
    }

    // 创建快照弹窗
    .create-snapshot {
        .top {
            .title {
                font-size: 18px;
                font-weight: 500;
                color: #040E34;
            }
        }

        .main {
            // margin-top: 40px;

            .form {
                .row {
                    margin-top: 24px;

                    .ant-input {
                        width: 95%;
                    }

                    .ant-select {
                        width: 90%;
                        // background: #F2F2F7;
                    }

                    // .ant-select-selector {
                    //     // background: #F2F2F7;
                    // }

                    .label {
                        height: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        font-size: 13px;
                        color: #040E34;
                    }
                }
            }
        }

        .footer {
            display: flex;
            justify-content: center;
            // flex-direction: row-reverse;
            margin-top: 48px;

            .cancel-btn {
                height: 40px;
                background: #F2F2F7;
                border-radius: 4px;
                border: none;
                color: #525666;
            }

            .sub-btn {
                height: 40px;
                background: #696AE6;
                border-radius: 4px;
                color: #FFFFFF;
                margin-right: 8px;
            }
        }
    }

}

// 删除弹窗
.snapshot-del-icon {
    color: red !important;
    border-color: red !important;
}

.snapshot-cancel-button {
    height: 0.4rem;
    border: 1px solid #D9DBE1 !important;
    background: #FFF !important;
    color: #525666 !important;
    font-size: .16rem !important;
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
}
@primary-color: #5C5EE4;